.main {
    margin: 10px auto auto 10px;
}

.dropbtns {
    width: 100px;
    font-size: 15px;
    margin: 3px;
    position: relative;
    display: inline-block;
    color: #777674;
    font-weight: bold;
    text-decoration: none;
    text-shadow: rgba(255, 255, 255, .5) 1px 1px, rgba(100, 100, 100, .3) 3px 7px 3px;
    user-select: none;
    outline: none;
    border-radius: 3px / 100%;
    background-image: linear-gradient(45deg, rgba(255, 255, 255, .0) 30%, rgba(255, 255, 255, .8), rgba(255, 255, 255, .0) 70%),
    linear-gradient(to right, rgba(255, 255, 255, 1), rgba(255, 255, 255, 0) 20%, rgba(255, 255, 255, 0) 90%, rgba(255, 255, 255, .3)),
    linear-gradient(to right, rgba(125, 125, 125, 1), rgba(255, 255, 255, .9) 45%, rgba(125, 125, 125, .5)),
    linear-gradient(to right, rgba(125, 125, 125, 1), rgba(255, 255, 255, .9) 45%, rgba(125, 125, 125, .5)),
    linear-gradient(to right, rgba(223, 190, 170, 1), rgba(255, 255, 255, .9) 45%, rgba(223, 190, 170, .5)),
    linear-gradient(to right, rgba(223, 190, 170, 1), rgba(255, 255, 255, .9) 45%, rgba(223, 190, 170, .5));
    background-repeat: no-repeat;
    background-size: 200% 100%, auto, 100% 2px, 100% 2px, 100% 1px, 100% 1px;
    background-position: 200% 0, 0 0, 0 0, 0 100%, 0 4px, 0 calc(100% - 4px);
    box-shadow: rgba(0, 0, 0, .5) 3px 10px 10px -10px;
}

.dropbtns:hover {
    transition: .5s linear;
    background-position: -200% 0, 0 0, 0 0, 0 100%, 0 4px, 0 calc(100% - 4px);
}

.dropbtns:active {
    top: 1px;
}

.dropdown {
    position: relative;
    display: inline-block;
}

.content {
    display: none;
    position: absolute;
    background-color: #f1f1f1;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
    z-index: 1;
}

.content .navlink {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
}

.content .navlink:hover {
    background: -webkit-linear-gradient(135deg, rgb(247, 241, 241) 48%, rgb(55, 210, 217) 98%);
    background: -moz-linear-gradient(135deg, rgb(247, 241, 241) 48%, rgb(55, 210, 217) 98%);
    background: linear-gradient(135deg, rgb(247, 241, 241) 48%, rgb(55, 210, 217) 98%);
}

.dropdown:hover .content {
    display: flex;
}

.dropdown:hover .dropbtns {
    background-color: rgba(253, 255, 136, 0.5);
    box-shadow: 0 0 30px rgba(71, 69, 72, 0.8)
}

.navlink {
    border: 1px solid black;
    margin: 1px;
    font-size: 14px;
}